  .trip_details__country-name {
    width: 150px;
    font-size: 15px;
  }
}

@media (max-width: 638px) {
  /*new trip form*/

  /* .new-trip__form {
    height: 40%;
  } */
  /*error home page*/
  .error__container {
    font-size: 35px;
    line-height: 80px;
  }
  .all-trips__header {
    font-size: 30px;
  }

  .trip_details {
    margin-top: 5px;
    height: 150px;
  }
  .trip_details__country-name {
    font-size: 20px;
  }

  .trip_delete-button {
    width: 50%;
    padding: 8px;
    font-size: 10px;
  }

  .already-exist h1 {
    font-size: 35px;
  }

  /*Country details*/
  .trip-details__header {
    width: 95%;
    margin-top: 0px;
  }

  @-webkit-keyframes toRight {
    from {
      width: 0;
    }
    to {
      width: 95%;
    }
  }

  @keyframes toRight {
    from {
      width: 0;
    }
    to {
      width: 95%;
    }
  }
  /* .trip-details__country {
    font-size: 40px;
  } */

  .trip_details__country-name {
    width: 110px;
    font-size: 20px;
  }
}
@media (max-width: 480px) {
  /*Home Page*/
  .all-trips__second {
    font-size: 15px;
  }
  .add-trip__icon {
    padding: 6px;
    width: 150px;
  }
  .all-trips_message {
    margin-top: 20px;
    font-size: 30px;
  }
  /*new trip form*/
  .new-trip__form-container {
    width: 75%;
    padding: 0 20px;
  }
  /*error home page*/
  .error__container {
    font-size: 30px;
    line-height: 60px;
  }

  .all-trips__header {
    font-size: 22px;
  }

  .trips-list__container {
    grid-template-columns: repeat(2, 1fr);
  }

  .already-exist h1 {
    font-size: 30px;
  }

  /*Country details*/
  .trip-details__country {
    font-size: 15px;
  }

  .country-capital h2 {
    font-size: 10px;
  }
  .country-capital p {
    font-size: 12px;
  }

  /*new plan*/
  .new-plan__container {
    width: 70%;
    height: 80%;
  }
}

* {
  padding: 0px;
  margin: 0px;
  box-sizing: border-box;
}
body {
  background-image: url(/triplan-react-redux-firebase/static/media/22.c6eee0ab.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  min-height: 100vh;
  background-attachment: fixed;
}

body::-webkit-scrollbar {
  width: 0.8em;
}
body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px rgb(0, 0, 0);
}
body::-webkit-scrollbar-thumb {
  background-color: rgba(185, 158, 69, 0.801);
  /* border: 1px solid rgb(0, 0, 0); */

  border-radius: 10px;
}

