@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap");

.plan__container {
  border: 1px solid rgba(0, 0, 0, 0.479);
  width: 100%;
  display: flex;
  align-items: center;
  margin-top: 10px;
  padding: 10px;
  box-shadow: 0px 0px 2px black;
  border-radius: 5px;
  background-image: linear-gradient(
    200deg,
    rgba(0, 0, 0, 0.801),
    rgba(53, 53, 53, 0.74),
    rgba(54, 54, 54, 0.623)
  );
  animation: show 0.5s;
}

@keyframes show {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.plan__container section {
  width: 93%;
  display: flex;
  align-items: center;
}
.plan__day {
  color: rgb(255, 219, 102);

  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  border-right: 1px solid rgb(255, 219, 102);
  padding-right: 10px;
}
.plan__plan-details {
  padding-left: 10px;
  overflow: auto;
}
h1 {
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  margin: 0px;
}

h2 {
  color: rgb(209, 209, 209);

  padding: 2px;
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  margin: 0px;
}
.plan__delete-button {
  border-radius: 3px;
  padding: 10px;
  margin-left: 5px;
  width: 7%;
  background-image: linear-gradient(
    45deg,
    rgba(245, 85, 85, 0.795),
    rgba(255, 49, 49, 0.301)
  );
  height: 100%;
  border: none;
  cursor: pointer;
  font-family: "Poppins", sans-serif;
  display: flex;
  justify-content: center;
}
.plan__delete-button:hover {
  box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
  /* border: 1px solid black; */
  background-image: linear-gradient(
    to bottom,
    rgba(252, 89, 89, 0.685),
    rgba(255, 82, 82, 0.747)
  );
}
.plan__delete-button p {
  padding: 0;
  margin: 0;
}
.plan__delete-button i {
  display: none;
  margin: auto;
}
