@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap");
.new-plan__container {
  position: absolute;
  right: 0;
  top: 100px;
  height: 65%;
  width: 25%;
  background-image: linear-gradient(
    to top,
    rgb(213, 239, 245),
    rgb(107, 118, 121)
  );
  box-shadow: 0px 0px 10px black;
  border-radius: 15px;
  padding: 20px 60px;

  animation: show 200ms;
  display: flex;
  /* align-items: space-between; */
  justify-content: center;
}

@keyframes show {
  from {
    opacity: 0;
    width: 0%;
  }
  to {
    opacity: 1;
    width: 100%;
  }
}
.new-plan__form-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: space-around;
}
.new-plan__header {
  margin: 15px 0;
  text-align: center;
  color: rgb(0, 0, 0);
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  font-size: 32px;
}

.new-trip__form {
  width: 100%;
}

i.icon.window.close:before {
  font-size: 25px;
}
.new-plan__h1 {
  text-align: center;
  color: rgb(0, 0, 0);
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  width: 100%;
}
.textarea-container {
  resize: none;
  padding: 5px;
  background-color: #e6faf7d7;
  width: 100%;
}
.text-area {
  width: 100%;
}
.days-options {
  display: flex;
  justify-content: start;
}

.error_message {
  position: absolute;
  color: red;
}
