.all-trips__container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  flex-direction: column;
  font-family: "Comfortaa", sans-serif;
}
.all-trips__header {
  font-size: 65px;
  color: rgba(255, 219, 102, 0.801);

  /* color: rgba(0, 0, 0, 0.781); */
  text-shadow: 0px 0px 10px rgb(0, 0, 0);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.795),
    rgba(0, 0, 0, 0.199),
    rgba(0, 0, 0, 0)
  );
  text-align: center;
  border-radius: 20px;
  padding: 5px 10px;
}

.add-trip__icon {
  box-shadow: 0px 0px 80px black, inset 0px 0px 42px black;
  padding: 10px;
  width: 220px;
  border: 1px solid rgb(71, 71, 71);
  border-radius: 5px;
  font-size: 24px;
  background-color: rgba(0, 0, 0, 0.226);
  /* color: rgb(65, 65, 65); */
  cursor: pointer;
  animation: display 800ms;
  transition: 500ms ease-in-out;
}
@keyframes display {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.add-trip__icon:hover {
  box-shadow: 0px 0px 80px black, inset 0px 0px 3px black;
  background-color: rgba(0, 0, 0, 0);

  transition: 500ms ease;
}
.add-trip__icon:hover .all-trips__second {
  text-shadow: 0px 0px 15px rgb(255, 255, 255);
  color: rgb(51, 51, 51);
}

i.plus.icon.cancel::before {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.all-trips__second {
  font-size: 25px;
  color: rgb(214, 214, 214);
  text-shadow: 0px 0px 15px rgb(187, 187, 187);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
  text-align: center;
}
.all-trips__second:hover {
  /* text-shadow: 0px 0px 15px rgb(255, 255, 255);
  color: rgb(51, 51, 51); */
}
.add-trip__icon i {
  margin: 0;
}
.add-trip__icon h2 {
  font-family: "Comfortaa", sans-serif;
}
.all-trips_message {
  font-family: "Comfortaa", sans-serif;
  margin-top: 150px;
  width: 60%;
  font-size: 60px;
  color: rgba(255, 219, 102, 0.801);
  text-shadow: 0px 0px 6px rgb(0, 0, 0);
  font-weight: 700;
  font-family: "Comfortaa", cursive;

  animation: show-message 2s;
}

@keyframes show-message {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
