@media (max-width: 1572px) {
  .trip_details__country-name {
    width: 250px;
  }
}

@media (max-width: 1320px) {
  .trip_details__country-name {
    width: 230px;
    font-size: 35px;
  }
}

@media (max-width: 1300px) {
  /*Home Page*/
  .new-trip__form-header {
    font-size: 45px;
  }
  .new-trip__form-country {
    font-size: 25px;
  }
  .input_note {
    font-size: 12px;
  }

  .all-trips__header {
    font-size: 50px;
  }

  .trip-details__country {
    font-size: 55px;
  }
  /*Country page*/
  .plan__day {
    font-size: 25px;
  }
  .plan__plan-details {
    padding-left: 10px;
    overflow: auto;
    font-size: 20px;
  }
  .plan__delete-button {
    font-size: 10px;
  }
  .trip_details__country-name {
    width: 200px;
    font-size: 30px;
  }
}

@media (max-width: 1200px) {
  /*Home Page*/
  .all-trips__second {
    font-size: 20px;
  }
  .all-trips_message {
    margin-top: 80px;
    font-size: 50px;
  }
  /*new trip form*/
  .new-trip__form-container {
    width: 60%;
    height: 60%;
  }
  .new-trip__form-header {
    font-size: 35px;
  }
  .new-trip__form-country {
    font-size: 20px;
  }
  .input_note {
    font-size: 10px;
  }
  /*error home page*/
  .error__container {
    font-size: 55px;
  }

  .all-trips__header {
    font-size: 50px;
  }

  /*Country page*/
  .plan__day {
    font-size: 23px;
  }
  .plan__plan-details {
    padding-left: 10px;
    overflow: auto;
    font-size: 18px;
  }

  /*new plan*/
  .new-plan__container {
    padding: 20px 40px;
  }

  .new-plan__header {
    font-size: 25px;
  }
  .new-plan__h1 {
    font-size: 20px;
  }
}

@media (max-width: 1062px) {
  /*Home Page*/
  .add-trip__icon {
    padding: 6px;
    width: 200px;
  }
  .new-trip__form-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .new-trip__form {
    width: 100%;
  }
  .all-trips__header {
    font-size: 40px;
  }
  .trips-list__container {
    grid-template-columns: repeat(3, 1fr);
    width: 80%;
    gap: 20px;
  }
  .trip_details {
    margin-top: 5px;
    height: 200px;
  }
  .trip_details__country-name {
    font-size: 25px;
  }

  /*Country page*/
  .plan__delete-button {
    padding: 5px;
  }
  .trip-details__header {
    flex-direction: column;
  }
  .country-capital h2 {
    font-size: 18px;
  }
  .country-capital p {
    font-size: 20px;
  }
  .trip-details__country {
    font-size: 50px;
  }
}
@media (max-width: 880px) {
  /*Home page*/
  .add-trip__icon {
    padding: 6px;
    width: 180px;
  }
  /*error home page*/
  .error__container {
    font-size: 50px;
    line-height: 120px;
  }

  /*Country page*/
  .plan__day {
    font-size: 20px;
  }
  .plan__plan-details {
    padding-left: 10px;
    overflow: auto;
    font-size: 15px;
  }

  .plan__delete-button p {
    font-size: 8px;
  }
  .trip-details__country {
    font-size: 35px;
  }
  /*new plan*/
  .new-plan__container {
    width: 40%;
    padding: 10px 30px;
  }

  .new-plan__header {
    font-size: 20px;
  }
  .new-plan__h1 {
    font-size: 15px;
  }
  i.icon.window.close:before {
    font-size: 20px;
  }
  .trip_details__country-name {
    width: 180px;
    font-size: 20px;
  }
}
@media (max-width: 768px) {
  /*header*/
  .navigation-bar {
    height: 50px;
  }
  /*Logo*/
  .logo {
    width: 100px;
    height: 50px;
    background-size: contain;
  }
  /*Home Page*/

  .all-trips_message {
    margin-top: 50px;
    text-align: center;
    font-size: 40px;
  }
  /*new trip form*/
  .new-trip__form-container {
    width: 75%;
    padding: 0 50px;
  }
  .new-trip__form-header {
    font-size: 30px;
  }
  .new-trip__form-country {
    font-size: 15px;
  }
  .th {
    width: 100px;
    height: 10px;
  }
  .input_note {
    font-size: 10px;
  }

  /*error home page*/
  .error__container {
    font-size: 40px;
    line-height: 100px;
  }

  .already-exist h1 {
    font-size: 40px;
  }
  /*Country details*/
  .trip-details__header {
    width: 90%;
    margin-top: 0px;
  }

  @keyframes toRight {
    from {
      width: 0;
    }
    to {
      width: 90%;
    }
  }
  .trip-details__country {
    font-size: 30px;
  }

  /*Country page*/
  .plan__day {
    font-size: 15px;
  }
  .plan__plan-details {
    padding-left: 10px;
    overflow: auto;
    font-size: 10px;
  }

  .plan__delete-button p {
    display: none;
  }
  .plan__delete-button i {
    display: block;
  }

  /*new plan */
  .new-plan__container {
    top: 70px;
  }

  .trip_details__country-name {
    width: 150px;
    font-size: 15px;
  }
}

@media (max-width: 638px) {
  /*new trip form*/

  /* .new-trip__form {
    height: 40%;
  } */
  /*error home page*/
  .error__container {
    font-size: 35px;
    line-height: 80px;
  }
  .all-trips__header {
    font-size: 30px;
  }

  .trip_details {
    margin-top: 5px;
    height: 150px;
  }
  .trip_details__country-name {
    font-size: 20px;
  }

  .trip_delete-button {
    width: 50%;
    padding: 8px;
    font-size: 10px;
  }

  .already-exist h1 {
    font-size: 35px;
  }

  /*Country details*/
  .trip-details__header {
    width: 95%;
    margin-top: 0px;
  }

  @keyframes toRight {
    from {
      width: 0;
    }
    to {
      width: 95%;
    }
  }
  /* .trip-details__country {
    font-size: 40px;
  } */

  .trip_details__country-name {
    width: 110px;
    font-size: 20px;
  }
}
@media (max-width: 480px) {
  /*Home Page*/
  .all-trips__second {
    font-size: 15px;
  }
  .add-trip__icon {
    padding: 6px;
    width: 150px;
  }
  .all-trips_message {
    margin-top: 20px;
    font-size: 30px;
  }
  /*new trip form*/
  .new-trip__form-container {
    width: 75%;
    padding: 0 20px;
  }
  /*error home page*/
  .error__container {
    font-size: 30px;
    line-height: 60px;
  }

  .all-trips__header {
    font-size: 22px;
  }

  .trips-list__container {
    grid-template-columns: repeat(2, 1fr);
  }

  .already-exist h1 {
    font-size: 30px;
  }

  /*Country details*/
  .trip-details__country {
    font-size: 15px;
  }

  .country-capital h2 {
    font-size: 10px;
  }
  .country-capital p {
    font-size: 12px;
  }

  /*new plan*/
  .new-plan__container {
    width: 70%;
    height: 80%;
  }
}
