@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap");

.trips-list__container {
  margin-top: 50px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  width: 60%;
  gap: 20px;
}
.trip__container {
  position: relative;
}
.trip_details {
  font-family: Comfortaa;
  margin-top: 5px;
  border-radius: 6px;
  background-image: linear-gradient(
    200deg,
    rgba(0, 0, 0, 0.801),
    rgba(53, 53, 53, 0.432),
    rgba(54, 54, 54, 0.178)
  );
  box-shadow: inset 0px 0px 18px rgb(0, 0, 0);
  width: 100%;
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;

  filter: grayscale(60%);
  transition: 500ms ease-in-out;
}
.trip_details:hover {
  box-shadow: inset 0px 0px 80px rgb(0, 0, 0);
  transition: 500ms ease;
  filter: grayscale(0%);
}

.trip_details__country-name {
  display: block; /* Fallback for non-webkit */
  max-width: 400px;
  margin: 0 auto;
  font-size: 35px;
  line-height: 1.4;
  text-overflow: ellipsis;

  overflow: hidden;
  text-align: center;
  color: rgb(255, 219, 102);
}
.multiple-words {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.trip_delete-button {
  position: absolute;
  border-radius: 2px;
  padding: 10px;
  background-image: linear-gradient(
    45deg,
    rgba(245, 85, 85, 0.795),
    rgba(255, 49, 49, 0.301)
  );
  border: none;
  cursor: pointer;
  width: 60%;
  margin-top: -50px;
  left: 50%;
  transform: translateX(-50%);
}
.trip_delete-button:hover {
  box-shadow: inset 0px 0px 3px rgb(0, 0, 0);
  /* border: 1px solid black; */
  background-image: linear-gradient(
    to bottom,
    rgba(252, 89, 89, 0.685),
    rgba(255, 82, 82, 0.747)
  );
}
