.trip-details__container {
  /* border: 1px solid red; */
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}
.trip-details__header {
  color: rgba(255, 219, 102, 0.801);

  text-shadow: 0px 0px 10px rgb(0, 0, 0);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.795),
    rgba(0, 0, 0, 0.199),
    rgba(0, 0, 0, 0)
  );
  text-align: center;
  border-radius: 20px;
  padding: 5px 10px;
  display: flex;
  flex-direction: column;
  width: 80%;
  margin-top: 20px;
  justify-content: center;
  align-items: center;

  border-radius: 5px 5px 15px 15px;
  animation: toRight 3s;
}

@keyframes toRight {
  from {
    width: 0;
  }
  to {
    width: 80%;
  }
}
.trip-details__country {
  white-space: nowrap;
  max-width: 60%;
  /* border: 1px solid red; */
  overflow: hidden;
  text-overflow: ellipsis;

  font-size: 65px;
  color: rgba(255, 219, 102, 0.801);
  text-shadow: 0px 0px 10px rgb(0, 0, 0);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
  text-align: center;
  padding: 15px 30px;
  margin-top: 30px;
  opacity: 1;
  animation: display 4s;
  /* display: flex;
  flex-direction: column; */
}

@keyframes display {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.country-flag {
  border-radius: 8px;
  animation: display 4s;
  width: 15%;
  /* height: ; */

  /* box-shadow: 0 0 8px rgba(0, 0, 0, 1); */
}
.country-capital {
  animation: display 4s;
  margin-left: 10px;
  font-size: 30px;
  text-shadow: 0px 0px 10px rgb(0, 0, 0);

  color: rgba(255, 219, 102, 0.801);
}
.cancel {
  transform: rotate(90deg);

  /* transition: linear 1s; */
}
.trip-details__add-text {
  font-size: 25px;
  color: rgb(65, 65, 65);
  text-shadow: 0px 0px 10px rgb(0, 0, 0);
  font-weight: 700;
  font-family: "Comfortaa", cursive;
  text-align: center;
  /* padding: 15px 30px; */
  /* margin-top: 30px; */
}
