@import url("https://fonts.googleapis.com/css2?family=Comfortaa:wght@700&display=swap");

.new-trip__container {
  z-index: 100;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.52),
    rgb(93, 93, 93)
  );
  display: flex;
  justify-content: center;
  align-items: center;
}
.close-from-outside {
  background-color: rgba(111, 0, 255, 0);
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
}
.new-trip__form-container {
  height: 60%;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  background-image: linear-gradient(
    to bottom,
    rgba(241, 241, 241, 0.87),
    rgb(207, 207, 207)
  );
  border-radius: 5px;
  position: relative;
  padding: 50px 100px;
}

.new-trip__form-header {
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  font-size: 50px;
  text-align: center;
  /* margin-left: 100px; */
}
.new-trip__form-country {
  font-family: "Comfortaa", sans-serif;
  font-weight: 700;
  font-size: 30px;
}
.new-trip__form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60%;
  height: 80%;
  border-radius: 5px;
}
.new-trip__form section {
  display: flex;
  align-items: center;
  justify-content: center;
}
.close {
  position: absolute;
  top: 20px;
  right: 30px;
  font-size: 60px;
}
.input_note {
  color: red;
}
.css-1poimk-MuiPaper-root-MuiMenu-paper-MuiPaper-root-MuiPopover-paper {
  width: 350px;
}
.css-1poimk-MuiPaper-root-MuiMenu-paper-MuiPaper-root-MuiPopover-paper {
  height: 350px;
}
